.wrapper {
    display: flex;
    align-items: center;
    justify-content: space-between;
    font-family: monospace;
    position: absolute;
    top: 0;
    width: 100vw;
    height: 10vh;
    background-color: black;
}

.title {
    color: black;
    font-family: monospace;
    font-size: 1.5rem;
    background-color: white;
    padding: 0.2rem;
    padding-right: 1rem;
}

.navbar {
    display: flex;
    gap: 1rem;
    padding: 1rem;
}

.navbar a {
    text-decoration: none;
    color: white;
    font-family: monospace;
    font-size: 1.2rem;
}

.navbar a:hover {
    text-decoration: underline;
}

@media only screen and (max-width: 800px) {
    .navbar {
        display: none;
    }

    .title {
        margin-left: auto;
    }
}

@media only screen and (max-height: 500px) {
    .title {
        font-size: 1rem;
    }
}